.contact-us {
  &__gridWrapper {
    @include pageGrid;
    row-gap: 48px;
  }

  &__input-form {
    grid-column: 1 / -1;

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__input {
    width: 100%;
    box-sizing: border-box;
    padding: 14px 16px;
    border: 1px solid #f8f8fa;
    border-radius: 8px;
    background-color: #f8f8fa;

    color: $main-text-color;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;

    &:hover {
      border-color: #d5d7de;
    }

    &:focus {
      border-color: #292929;
      outline: none;
    }

    &::placeholder {
      color: #7e7e83;
      font-size: 14px;
      font-weight: 400;
      line-height: 140%;
    }

    &--small {
      height: 48px;
      margin-bottom: 16px;
    }

    &--big {
      resize: none;
      height: 148px;
      margin-bottom: 32px;
    }
  }

  &__contacts {
    grid-column: 1 / -1;

    @include onDesktop {
      grid-column: 7 / -1;
    }
  }

  &__title {
    margin-bottom: 8px;
    color: #9393a3;
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
  }

  &__info {
    position: relative;
    color: #191919;
    text-decoration: none;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    &::after {
      content: '';
      position: absolute;
      bottom: -4px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $main-text-color;

      transition: transform $effectDuration;
      transform: scale(0);
      transform-origin: right;
    }

    &:hover::after {
      transform: scale(1);
      transform-origin: left;
    }
  }
}
